<template>
	<div id="paotui-order-item">
		<div class="order-item">
			<div class="number-price flex-lr border-1px-b">
				<div class="number">#{{order.id}}</div>
				<div class="price">¥{{order.final_fee}}</div>
			</div>
			<router-link tag="div" class="order-detail border-1px-b" :to="util.getUrl({path: 'pages/plugin/paotui/detail', query: {id: order.id}})">
				<div class="padding-10-b">
					<div class="ilabel ilabel-primary" v-if="order.delivery_collect_type == 0 && order.deliveryer_id > 0">配送员抢单</div>
					<div class="ilabel ilabel-warning">{{order.order_type == 'buy' ? '代购' : '取送件'}}</div>
					<div class="ilabel ilabel-info" v-if="order.delivery_collect_type == 2">系统派单</div>
					<div class="ilabel ilabel-danger" v-if="order.delivery_collect_type == 3">转单</div>
				</div>
				<div class="detail-item buy-address flex-lr">
					<div class="left c-info">{{order.order_type == 'buy' ? '买' : '取'}}</div>
					<div class="right">
						<div class="store-title ellipsis">
							{{order.buy_address}}
						</div>
						<div class="store-address ellipsis">
							{{order.buy_username}} {{order.buy_mobile}}
						</div>
					</div>
				</div>
				<div class="detail-item accept-address flex-lr">
					<div class="left c-primary">送</div>
					<div class="right">
						<div class="store-title">
							{{order.accept_address}}
						</div>
						<div class="store-address">
							{{order.accept_username}} {{order.accept_mobile}}
						</div>
					</div>
				</div>
				<div class="detail-item flex-lr" v-if="order.deliveryer_id > 0 && order.deliveryer">
					<div class="left c-danger">配送:</div>
					<div class="right">
						<div class="store-title">
							{{order.deliveryer.title}} {{order.deliveryer.mobile}}
						</div>
						<div class="store-address" v-if="order.delivery_status == 2">
							已接单 (接单时间：{{order.delivery_assign_time_cn}})
						</div>
						<div class="store-address" v-else-if="order.delivery_status == 3">
							已取货 (取货时间：{{order.delivery_instore_time_cn}})
						</div>
						<div class="store-address" v-else-if="order.delivery_status == 4">
							已送达 (送达时间：{{order.delivery_success_time_cn}})
						</div>
					</div>
				</div>
				<div class="detail-item note flex-lr">
					<div class="left">备注</div>
					<div class="right">{{order.note ? order.note : '无'}}</div>
				</div>
				<div class="detail-item flex-lr">
					<div class="left">下单</div>
					<div class="right">{{order.addtime_cn}}</div>
				</div>
				<div class="detail-item flex-lr">
					<div class="left">配送</div>
					<div class="right">{{order.delivery_time}}</div>
				</div>
			</router-link>
			<ul class="btn-group" v-if="order.status < 3 || order.refund_status == 1 || order.refund_status == 2">
				<template v-if="order.status < 3">	
					<template v-if="order.status == 1">
						<router-link tag="li" :to="util.getUrl({path: 'pages/plugin/paotui/dispatch', query: {id: order.id}})" class="btn-item">调度</router-link>
					</template>
					<template v-if="order.status == 2">
						<router-link tag="li" :to="util.getUrl({path: 'pages/plugin/paotui/dispatch', query: {id: order.id}})" class="btn-item">重新调度</router-link>
						<li class="btn-item btn-primary" @click="onChangeOrderStatus(order, 'end', order_index)">完成订单</li>
					</template>
					<template v-if="order.is_pay == 1 && order.pay_type != 'delivery'">
						<li class="btn-item bg-danger" @click="onChangeOrderStatus(order, 'cancel_refund', order_index)">取消订单</li>
					</template>
					<template v-else>
						<li class="btn-item bg-danger" @click="onChangeOrderStatus(order, 'cancel', order_index)">取消订单</li>
					</template>
				</template>
				<template v-if="order.refund_status == 1">
					<li class="btn-item bg-danger" @click="onChangeOrderStatus(order, 'refund_handle', order_index)">发起退款</li>
					<li class="btn-item bg-danger" @click="onChangeOrderStatus(order, 'refund_status', order_index)">已退款</li>
				</template>
				<template v-else-if="order.refund_status == 2">
					<li class="btn-item bg-danger" @click="onChangeOrderStatus(order, 'refund_query', order_index)">查询退款</li>
					<li class="btn-item bg-danger" @click="onChangeOrderStatus(order, 'refund_status', order_index)">已退款</li>
				</template>
			</ul>
		</div>
	</div>
</template>

<script>
	export default {
		props: {
			order: Object,
			order_index: Number,
		},
		data() {
			return {
				isRefresh: false
			}
		},
		components: {
			
		},
		methods: {
			onChangeOrderStatus: function(order, type, index) {
				var params = {order: order, type: type, index: index};
				this.$emit('onChangeOrderStatus', params);
			}
		}
	}
</script>

<style>
	#paotui-order-item .order-item .ilabel{
		display: inline-block;
		font-size: 12px;
		padding: 3px 8px;
		border-radius: 50px;
		border: 1px solid #7a7a7a;
		margin-right: 5px;
	}
	#paotui-order-item .order-item .ilabel.ilabel-primary{
		color: #4FAE52;
		border-color: #4FAE52;
	}
	#paotui-order-item .order-item .ilabel.ilabel-warning{
		color: #f0ad4e;
		border-color: #f0ad4e;
	}
	#paotui-order-item .order-item .ilabel.ilabel-danger{
		color: #FF2C4C;
		border-color: #FF2C4C;
	}
	#paotui-order-item .order-item .ilabel.ilabel-info{
		color: #1AB5F5;
		border-color: #1AB5F5;
	}
	#paotui-order-item .order-item{
		background-color: #fff;
		margin-bottom: 10px;
		font-size: 15px;
	}
	#paotui-order-item .order-item .number-price{
		padding: 10px;
	}
	#paotui-order-item .order-item .number-price .number{
		font-size: 20px;
		color: #E87613;
	}
	#paotui-order-item .order-item .number-price .price{
		color: #EB0000;
	}
	#paotui-order-item .order-item .order-detail{
		padding: 10px 10px 0;
	}
	#paotui-order-item .order-item .order-detail .detail-item{
		padding-bottom: 10px;
	}
	#paotui-order-item .order-item .order-detail .left{
		width: 12%;
	}
	#paotui-order-item .order-item .order-detail .right{
		width: 88%;
	}
	#paotui-order-item .order-item .order-detail .note .right{
		line-height: 1.2;
	}
	#paotui-order-item .order-item .order-detail .store-address{
		margin-top: 10px;
		font-size: 14px;
		color: #c9c9c9;
	}
	#paotui-order-item .order-item .btn-group{
		padding: 10px;
		display: flex;
		align-items: center;
		justify-content: flex-end;
	}
	#paotui-order-item .order-item .btn-group .btn-item{
		min-width: 60px;
		margin-left: 10px;
		text-align: center;
		padding: 10px 8px;
		border: 1px solid #e5e5e5;
		border-radius: 3px;
		font-size: 14px;
	}
	#paotui-order-item .order-item .btn-group .btn-item.btn-primary{
		background-color: #4FAE52;
		border-color: #4FAE52;
		color: #fff;
	}
</style>


